<template>
<div>
    <a-space align="center">
        <a-typography-title :level="2"> Example </a-typography-title>
    </a-space>
</div>
<div>
    <a-space align="center" direction="vertical">
        <a-image-preview-group>
            <a-image :width="400" src="https://iamges-for-deeplearning.oss-ap-northeast-1.aliyuncs.com/object-detect/4426.jpg_wh300.jpg" />
            <a-image :width="400" src="https://iamges-for-deeplearning.oss-ap-northeast-1.aliyuncs.com/object-detect/tmp7jpdth8p.png" />
        </a-image-preview-group>
        <p> this is a sample </p>
    </a-space>
</div>
<div>
    <a-space align="center" direction="vertical">
        <a-image v-bind:src="'data:image/jpeg;base64,'+ imageBytes" />
        <a-upload v-model:file-list="imageList" 
            name="image"
            action="http://47.245.35.88:9000/detection/people"
            @change="handleChange"
        >
            <a-button>
                <upload-outlined> </upload-outlined>
                Click to Upload
            </a-button> 
        </a-upload>
    </a-space>
</div>
</template>
<script>
import { message } from 'ant-design-vue';
import { UploadOutlined } from '@ant-design/icons-vue';
import { defineComponent, ref} from 'vue'
export default defineComponent({
    components: {
        UploadOutlined,
    },
    setup() {
        const handleChange = info => {
            if (info.file.status === "done"){
                message.success(`file uploaded successfully`)
                imageBytes.value = info.file.response['img']
            }
            else if (info.file.status === 'error') {
                message.error(`file upload failed`)
            }
        };
        const imageList = ref([]);
        var imageBytes = ref("aaaaaaa")
        return {
            imageList,
            handleChange,
            imageBytes
        };
    },
});
</script>